// Создание новых страниц главного слайдера

function createMianFoto() {
	if (document.querySelector('.example-trends__model') !== null) {
		let sourseSlides = document.querySelectorAll('.example-trends__model');
		let placeToInsertNewSlides = document.querySelector('.mainFoto__slider');

		for (let index = 0; index < sourseSlides.length; index++) {
			let newSlide = document.createElement('div');
			newSlide.classList.add('mainFoto__slide', 'slider__slide');

			// Создаём и заполняем блок изображения
			let newSlideFoto = document.createElement('div');
			newSlideFoto.classList.add('mainFoto__image', 'image-mb', '_ibg');

			let fotoExample = document.querySelectorAll('.example-trends__foto')[index];
			let contentfotoExample = fotoExample.innerHTML;
			newSlideFoto.innerHTML = contentfotoExample;

			// Создаём и заполняем текстовый блок
			let newSlideTextBlock = document.createElement('div');
			newSlideTextBlock.classList.add('mainFoto__text-block');

			let numberExample = document.querySelectorAll('.example-trends__number')[index];
			let contentNumberExample = numberExample.innerHTML;
			let mainFotoNumber = document.createElement('div');
			mainFotoNumber.classList.add('mainFoto__number', 'h4');
			mainFotoNumber.innerHTML = contentNumberExample;
			newSlideTextBlock.append(mainFotoNumber);

			let textExample = document.querySelectorAll('.example-trends__text')[index];
			let contentTextExample = textExample.innerHTML;
			let mainFotoText = document.createElement('div');
			mainFotoText.classList.add('mainFoto__text', 'h4');
			mainFotoText.innerHTML = contentTextExample;
			newSlideTextBlock.append(mainFotoText);

			// Вставляем готовые блоки в основной блок слайдера
			newSlide.append(newSlideFoto);
			newSlide.append(newSlideTextBlock);

			placeToInsertNewSlides.append(newSlide);
		}
	}
}
createMianFoto();

// Создание текстового блока при ширинах меньших чем 1300 и более 768
function dublicate() {
	
	if (document.querySelector('.beautyModels__trends .mainFoto__text-block')) document.querySelector('.beautyModels__trends .mainFoto__text-block').remove();
	let activeMainFotoTextBlock = document.querySelector('.mainFoto__slider .swiper-slide-active .mainFoto__text-block').cloneNode(true);

	let placeToInsertMainFotoTextBlock;
	if (window.screen.width < 768) {
		placeToInsertMainFotoTextBlock = document.querySelector('.trends__link');
	} else placeToInsertMainFotoTextBlock = document.querySelector('.trends__text-row');
	

	placeToInsertMainFotoTextBlock.after(activeMainFotoTextBlock);
}
if (window.screen.availWidth < 1300 && window.screen.availWidth > 767) setTimeout(dublicate, 500);
