// 1 rem = 10 px
if (window.screen.width > 768) {
	let links = document.querySelectorAll('.menu__link');
	let menuLinks = document.querySelector('.menu__links');

	let placeMenuLinksLeft = menuLinks.offsetLeft;
	let placeMenuLinksTop = menuLinks.offsetTop;

	for (let index = 0; index < links.length; index++) {
		const element = links[index];
		element.addEventListener('click', function () {

			let wrapFigure = document.querySelectorAll('.menu__animation-wrap')[index];
			console.log(wrapFigure);

			let lineFigure = document.querySelectorAll('.menu__line ')[index];

			let placeBegin;
			let lenghtLineBegin;
			let lengthOfLink;

			lengthOfLink = element.offsetWidth;
			placeBegin = element.offsetLeft;
			lenghtLineBegin = 0;
			animationLink(lengthOfLink, element);

			function animationLink(lengthOfLink, element) {
				wrapFigure.style.cssText = `
			display: flex;
			left: ${(placeBegin + placeMenuLinksLeft - 6) / 10}rem;
			top: ${(placeMenuLinksTop + 10 + 18) / 10}rem;
		`;
				animationLine(lengthOfLink, 0, element);
			}

			function animationLine(lengthOfLink, startLength = 0, element) {
				let lazyLength = startLength;
				if (lengthOfLink / 10 > lazyLength) {
					lazyLength += 0.3;
					lineFigure.style.width = lazyLength + 'rem';
					setTimeout(animationLine, 10, lengthOfLink, lazyLength, element);
				} else setTimeout(cleanLine, 1000, lengthOfLink, lazyLength);
			}

			function cleanLine(lengthOfLink, lazyLength) {
				if (lengthOfLink / 10 >= lazyLength - 0.3 && lazyLength > 0.5) {
					lazyLength -= 0.2;
					lineFigure.style.width = lazyLength + 'rem';
					setTimeout(cleanLine, 10, lengthOfLink, lazyLength);
				} else wrapFigure.style.opacity = 0;
				return;
			}

		});
	}
}